@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family:'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.App main {
  width: 100%;
  height: 80%;
  display: flex;
  background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
}

.App main section {
  width: 50%;
  height: 100%;
  display: flex;

  align-items: center;
}

.App main .container-ecuaciones {
  background-color: #fff;
}

.App main .homepage {
  background-color: #fff;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.App main .homepage img {
  width: 25%;
}

.App main .container-show {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
}

@media (max-width: 800px) {
  .App main {
    height: 140vh;
    flex-direction: column;
  }
  .App main section {
    width: 100%;
  }

  .App main .container-ecuaciones {
    height: 70vh;
  }

  .App main .container-show {
    height: 70vh;
    width: 100%;
  }
  .App
    main
    .container-show
    .container-showFormula
    .container-formulaInterpolacion {
    flex-direction: column;
  }

  .App main .homepage {
    height: 60%;
  }
}

header {
  display: flex;
  width: 100vw;
  justify-content: center;
}

header .container-headerTitle {
  width: 45%;
  text-align: left;
}

header nav {
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

header nav a {
  color: #000;
}

header nav .dropdown {
  position: relative;
}

header nav .dropdown .dropdown-container {
  display: none;
}

header nav .dropdown .dropdown-container .dropdown-content {
  position: absolute;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-width: 100%;
  height: 90px;
  box-shadow: 0px 14px 13px 0px rgba(0, 0, 0, 0.2);
  border-radius: 0 0 15px 15px;
}

header nav .dropdown:hover > .dropdown-container {
  display: block;
}

header nav .dropdown .dropdown-container a {
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

header nav .dropdown .dropdown-container a:nth-child(2) {
  border-radius: 0 0 15px 15px;
}

header nav .dropdown .dropdown-container a:hover {
  background-color: #ddd;
}

header .container-headerTitle a {
  text-decoration: none;
  color: #000;
}

@media (max-width: 800px) {
  header {
    flex-direction: column;
  }
  header .container-headerTitle {
    width: 100%;
    text-align: center;
  }

  header nav {
    width: 100%;
    font-size: 0.9em;
  }
}

.container-intlineal {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.container-intlineal .container-form {
    width: 100%;
    height: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.container-intlineal .container-form .form-group {
    width: 45%;
    display: flex;
    flex-direction: column;
}
.container-intlineal .container-form .form-group label {
    display: flex;
    flex-direction: column;
    text-align: left;
    border-bottom: 5px;
}
.container-intlineal .container-form .form-group input {
    font-size: medium;
    border: 1px solid #ccc;
    border-radius: 0.25em;
    padding: 0.25em;
    height: calc(0.5em + .75rem + 2px);

    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.container-intlineal .container-form .form-group input:focus {
    box-shadow: 3px 4px 23px -1px rgba(94,94,99,0.51);
}

.container-show .container-showFormula .container-formula {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-show .container-showFormula .container-formula p {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-show .container-showFormula .container-formula .divition-lagrange {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-show .container-showFormula .container-formula .showDivition .separator {
    border-bottom: 1px solid #000;
}

.container-show .container-showFormula {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.container-show .container-showFormula .container-formulaInterpolacion {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 33%;
    font-size: 1.2em;
}

.container-formulaInterpolacion h4 {
    color: #fff;
    margin: 0;
}

.container-formulaInterpolacion .container-desarrolloFormula {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
}

.container-resultados {
    display: flex;
    justify-content: space-evenly;
    height: 33%;
}

.container-show .container-showFormula .container-resultados .container-formulaInterpolacion {
    height: 100%;
}

.buttonOrderType {
    border: none;
    color: #fff;
    background: #51A5FF;
}
